@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
.App{font-family:"Kanit", sans-serif;display:flex;align-items:center;flex-direction:column;grid-gap:30px;gap:30px}

.Form{padding:20px;border-radius:10px;background-color:rgba(243,227,85,0.57);display:flex;flex-direction:column;grid-gap:5px;gap:5px;min-width:300px}.Form__label{width:100%;display:flex;justify-content:space-between}.Form__input{border:2px solid #bbb;border-radius:10px;width:60px}

.Carousel{background-color:rgba(243,227,85,0.57);border-radius:10px}.Carousel__list{list-style-type:none;display:flex;padding:0;overflow:hidden}.Carousel__buttons{display:flex;justify-content:space-around;padding-bottom:5px}.Carousel__button{border-radius:10px;border:2px solid #bbb;width:60px;background-color:#fff}

